<template>
    <SubPage>
        <template v-slot:subpage>
            <section class="pt-page pt-page-5 pt-page-current" data-id="contact">
              <div class="section-title-block">
                <h2 class="section-title">Contact</h2>
                <h5 class="section-description">Get in Touch</h5>
              </div>
              <div class="row">
                <div class="col-sm-6 col-md-6 subpage-block">
                  <div class="block-title">
                    <h3>Get in Touch</h3>
                  </div>
                  <p>If you want to hire me or any query please fill the form I will contact with in 24 hours.</p>
                  <div class="contact-info-block">
                    <div class="ci-icon">
                      <i class="pe-7s-icon pe-7s-map-marker"></i>
                    </div>
                    <div class="ci-text">
                      <h5>Ansal Sushant City Panipat Haryana, India</h5>
                    </div>
                  </div>
                  <div class="contact-info-block">
                    <div class="ci-icon">
                      <i class="pe-7s-icon pe-7s-mail"></i>
                    </div>
                    <div class="ci-text">
                      <h5><a href="" class="" data-cfemail="">mohit.devrawal@gmail.com</a></h5>
                    </div>
                  </div>
                  <div class="contact-info-block">
                    <div class="ci-icon">
                      <i class="pe-7s-icon pe-7s-call"></i>
                    </div>
                    <div class="ci-text">
                      <h5>+91 7206437223</h5>
                    </div>
                  </div>
                  <div class="contact-info-block">
                    <div class="ci-icon">
                      <i class="pe-7s-icon pe-7s-check"></i>
                    </div>
                    <div class="ci-text">
                      <h5>Freelance Available</h5>
                    </div>
                  </div>
                </div>

                <div class="col-sm-6 col-md-6 subpage-block">
                  <div class="block-title">
                    <h3>Contact Form</h3>
                  </div>
                  <form id="contact-form" method="post" v-on:submit.prevent="submitContactForm">

                    <div class="messages"></div>

                    <div class="controls">
                      <div class="form-group">
                          <input id="form_name" type="text" name="user_name" v-model = "name" class="form-control" placeholder="Full Name" required="required" data-error="Name is required.">
                          <div class="form-control-border"></div>
                          <i class="form-control-icon fa fa-user"></i>
                          <div class="help-block with-errors"></div>
                      </div>

                      <div class="form-group">
                          <input id="form_email" type="email" v-model = "email" name="user_email" class="form-control" placeholder="Email Address" required="required" data-error="Valid email is required.">
                          <div class="form-control-border"></div>
                          <i class="form-control-icon fa fa-envelope"></i>
                          <div class="help-block with-errors"></div>
                      </div>

                      <div class="form-group">
                          <textarea id="form_message" name="message" class="form-control" placeholder="Message for me" rows="4" required="required" data-error="Please, leave me a message." v-model="message"></textarea>
                          <div class="form-control-border"></div>
                          <i class="form-control-icon fa fa-comment"></i>
                          <div class="help-block with-errors"></div>
                      </div>
                      <button class="button btn-send" v-if="sendButton === false">Send message</button>
                      <strong v-if="sendButton === true">
                      Notice: You already send Contact request to Mohit. Please wait for some time.</strong>
                    </div>
                  </form>
                </div>
              </div>
            </section>
        </template>
    </SubPage>
</template>
<script>
import SubPage from '../components/SubPage';
import emailjs from 'emailjs-com';
export default {
    components : {
        SubPage
    },
    data () {
        return {
            name: '',
            email: '',
            message: '',
            sendButton:false
        }
    },
    methods: {
        submitContactForm(event)  {
            localStorage.setItem("mr-folio", window.btoa('1'));
            emailjs.sendForm('service_n0byrpl', 'template_rbs097h', event.target, 'user_qKtz37hkX7MUNA8gbj1Zb')
                .then(() => {
                    this.$swal('Thanks For Contacting Mohit', 'Mohit will contact with within 24 Hours', 'success');
                    this.clearData();
                }, () => {
                    this.$swal('Something went wrong!!', 'Please try again after sometime', 'error');
                });
        },
        clearData() {
            this.name = this.email = this.message = "";
        }
    },
    mounted: function() {
        let checkingEmailStatus = window.atob(localStorage.getItem('mr-folio'));
        if (parseInt(checkingEmailStatus) > 0) {
            this.sendButton = true;
        }
    }
}
</script>
<style>
strong{
    weight:bold;
}
</style>